export type IconName = keyof typeof icons

export function Icon({ name, className }: { name: IconName; className?: string }) {
  const { w, h, d } = icons[name]
  return (
    <svg
      viewBox={`0 0 ${w} ${h}`}
      fill="currentColor"
      className={className}
      aria-hidden="true"
    >
      <path d={d} />
    </svg>
  )
}
